
.product{padding:.8rem;box-sizing:border-box
}
.product .mask{height:8.66667rem;overflow:hidden
}
.product .mask>img{display:block;width:100%
}
.product .btns{margin-top:.66667rem;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-align-items:center;-ms-flex-align:center;align-items:center
}
