
.home{width:100%
}
.home .wrapper{padding:30px 1.33333rem;box-sizing:border-box
}
.home .tit{margin-bottom:.8rem;width:7.33333rem;height:4.53333rem;background:url(titleBg.png) no-repeat;background-size:7.33333rem 4.53333rem;text-indent:-9999em
}
.home .btn{display:block;margin-bottom:.66667rem;width:100%;height:1.17333rem;line-height:1.17333rem;border:none
}
.home .btns>input{border:1px solid #25878b;border-radius:50px;text-align:center;box-shadow:0 3px 6px 0 rgba(0,0,0,0.1)
}
.home .btns .generate{background:url(generateBtn.png) no-repeat;background-size:7.33333rem 1.17333rem;text-indent:-9999em
}
.home .pic{position:absolute;left:1.33333rem;right:1.33333rem;bottom:2.93333rem;height:2.66667rem;background:url(bottom-pic.png) no-repeat;background-size:contain
}
.canvas-container{position:relative;width:720px;height:960px;background-repeat:no-repeat;text-align:center
}



